*,*::before,*::after{box-sizing:border-box}html,body{margin:0;padding:0}html{font-size:100%}body{position:relative;min-height:100vh;overflow-x:hidden;background-color:#474747;color:#e3d5ba}body,button,input,textarea{font-family:"Dosis", sans-serif}h1,p{text-align:center}h1{font-size:1.8em;line-height:1.1;margin:0 0 0.3em}p{margin:0 0 0.3em}strong{color:#e86701;font-weight:bold}a{color:#e86701;text-decoration:none}a:hover{color:#e3d5ba;transition:color 0.15s ease}small{font-size:60%}big{font-size:120%}hr{width:100%;border:1px dashed #e86701}label,input{display:inline-block;margin:0 0 0.3em;width:100%;max-width:300px}.form-control{margin-bottom:1em}label{color:#e86701;font-weight:bold}input[type="text"]{border-radius:5px;font-size:0.8em;padding:0.4em 0.5em;line-height:1;color:#e3d5ba;background-color:#3a3a3a;border:1px solid #e3d5ba}input[type="text"]:focus{background-color:#2e2e2e;border-color:#3a3a3a;outline:0;box-shadow:inset 0 1px 1px rgba(71,71,71,0.075),0 0 4px rgba(232,103,1,0.8)}form{text-align:center}button{font-size:1em;display:inline-block;color:#474747;border:2px solid #3a3a3a;background:#e86701;margin:0 0.5em 0.5em 0;padding:0.2em 0.5em;border-radius:5px;-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none;transition:0.15s ease;transition-property:background-color, border-color, color}button:hover{background:#fe7305}button:active{background:#cf5c01}button:focus{outline-color:rgba(232,103,1,0.8)}button.inverted{color:#e86701;border:2px solid #cf5c01;background:#474747}button.inverted:hover{background:#545454}button.inverted:active{background:#3a3a3a}button.inverted:focus{outline-color:rgba(71,71,71,0.8)}.keycode{display:inline-block;vertical-align:middle;margin-left:0.5em;font-size:60%;font-family:monospace;line-height:1}.keycode::before,.keycode::after{color:#e3d5ba}.keycode::before{content:"["}.keycode::after{content:"]"}
